<template>
  <div class="zero">
    <!-- 顶部图片 -->
    <img class="zero__img-auto header" src="@/assets/Slim/detail1.png" />
    <div class="bindPhone" v-if="!userInfo.mobile">
      <VanForm
        @submit="onSubmit"
        @failed="showErrorMsg"
        ref="form"
        :show-error="false"
        validate-first
        :show-error-message="false">
        <VanField
          v-model="form.phone"
          name="phone"
          placeholder="请填写手机号"
          :rules="[
            { required: true, message: '33003' },
            {
              pattern: phoneReg,
              message: '33004',
            },
          ]" />
        <div class="wrap">
          <VanField
            class="phonecode"
            v-model="form.code"
            name="code"
            placeholder="请填写验证码"
            :rules="[{ required: true, message: '33005' }]">
          </VanField>
          <CountDown :before-send="beforeSend" text="点击获取验证码" />
        </div>
      </VanForm>
    </div>
    <!-- 底部图片 -->
    <div class="content">
      <img
        src="@/assets/Slim/detail2.png"
        class="zero__img-auto"
        alt="" />
      <img
        src="@/assets/Slim/detail3.png"
        class="zero__img-auto"
        alt="" />
      <img
        src="@/assets/Slim/detail4.png"
        class="zero__img-auto"
        alt="" />
      <img
        src="@/assets/Slim/detail5.png"
        class="zero__img-auto"
        alt="" />
      <img
        src="@/assets/Slim/detail6.png"
        class="zero__img-auto"
        alt="" />
    </div>
    <!-- 底部按钮 -->
    <!-- 底部按钮 -->
    <div class="footer1">
      <div class="text">
        <div class="price"><span class="num">7</span><span>元</span></div>
        <div class="tips">
          <div class="text_p">
            <!-- 原价199元<img src="@/assets/Slim/nums1.png" alt="" /> -->
          </div>
          <div class="text_b">
            <span>立即扫码报名吧</span><img src="@/assets/Slim/right.png" alt="" />
          </div>
        </div>
      </div>
      <div class="div2"><a class="baoming" @click="signUp">立即报名</a></div>
    </div>
    <VanDialog
      class="dialog"
      v-model="showDialog"
      :show-confirm-button="false">
      <div class="title">关注我们</div>
      <div class="mt__20 git-box">
        <img :src="require('@/assets/Slim/down.gif')" />
      </div>
      <div class="text">
        感谢您关注<span style="color: #029375">长轻体验营</span>课程 <br />
        本期名额已经满员，邀请您扫码关注<br />
        <span style="color: #029375">长轻体验营</span>公众号 <br />
        获取最新动态~
      </div>
      <img
        src="https://xiaobai-market.oss-cn-huhehaote.aliyuncs.com/Public/xun/qrcode/code.jpg"
        class="kefuCode"
        alt="" />
      <div class="code_text">长按识别二维码, 关注公众</div>
      <div class="button" @click="close">好的</div>
    </VanDialog>
  </div>
</template>

<script>
import swipeList, { originalConfig, codeMap } from './config'
import { getSearch } from '@/utils'
import wxAuth from '@/helper/Auth/wxAuth'
import { mapState } from 'vuex'
import CountDown from '@/components/common/CountDown'
export default {
  components: {
    CountDown
  },
  data () {
    return {
      stateIsShow: getSearch('medId') === '1610', // 1610 快手渠道
      stateModelShow: false, // 声明弹窗 快手渠道需要显示此弹窗
      ...originalConfig, // 埋点数据
      swipeList, // 轮播数组
      paytype: 1, // 支付类型,
      showDialog: false,
      form: {
        phone: '', // 手机号
        code: '' // 验证码
      }
    }
  },
  async beforeRouteEnter (to, from, next) {
    await wxAuth({
      getTokenQuery: { ...getSearch() }
    })
    next()
  },
  computed: {
    ...mapState('user', ['userInfo'])
  },
  async created () {
    const {
      id,
      unionid,
      wx_body: { openid }
    } = this.userInfo
    this.$log.update({
      openid,
      unionid,
      uid: id,
      mid: id
    })
    this.checkOrderInWx()
    console.log(this.userInfo)
  },
  mounted () {
    this.initShareData()
  },
  methods: {
    signUp () {
      if (this.userInfo.mobile) return this.pay()
      return this.$refs.form.submit()
    },
    // 获取验证码
    beforeSend (start) {
      this.$refs.form
        .validate('phone')
        .then((res) => {
          start()
          this.$apis.userOperator.getPhoneCode({ phone: this.form.phone, product_sn: 'PRODUCT_SN_CQ_7' })
        })
        .catch((err) => {
          console.log(err)
          this.showMsg({ code: err.message })
        })
    },
    // 错误表单提示
    showErrorMsg (err) {
      err = { code: err.errors[0].message }
      this.showMsg(err)
    },
    // 弹出提示
    showMsg (err) {
      const { msg, code } = err
      let confirmButtonText = '确定'
      if (err.code === 30006) {
        confirmButtonText = '查看分班'
      }
      this.$dialog({ message: msg || codeMap[code].msg, confirmButtonText })
    },
    onSubmit (e) {
      // 表单校验后 绑定手机号 绑定成功则去支付
      this.$apis.baiduPcPayOrder.bindPhone(this.form)
        .then(async (res) => {
          this.pay()
        })
        .catch((err) => {
          this.showMsg(err)
        })
    },
    initShareData () {
      this.$wx.setShareData({
        title: '想轻松瘦身气质佳人，又不想反弹怎么办？', // 分享标题
        desc: '健康瘦身体验营马上开营啦！--7天掌握正确健康瘦身的秘诀', // 分享描述
        link: location.href, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
        imgUrl: 'https://xiaobai-market.oss-cn-huhehaote.aliyuncs.com/Public/xun/qrcode/changqing.png' // 分享图标
      })
    },
    checkOrderInWx () {
      return this.$apis.payment
        .checkOrderInWx({ product_sn: 'PRODUCT_SN_CQ_7' })
        .catch((err) => {
          this.payErrorBack(err)
          return Promise.reject(err)
        })
    },
    close () {
      this.showDialog = false
    },
    payErrorBack (data) {
      console.log(data)
      if (data.code === 36002) {
        this.showDialog = true
      }
      if (data.code === 30009 || data.code === 30013) {
        this.$router.push({
          name: 'ChangqingCode',
          query: {
            ...this.$route.query,
            productSn: 'PRODUCT_SN_CQ_7'
          }
        })
        return
      }
      if (codeMap[data.code]) {
        this.$dialog({ message: data.msg || codeMap[data.code].msg }).then(
          () => {
            // this.$log.click(codeMap[data.code].report)
          }
        )
      }
    },
    async pay () {
      if (this.paying) return
      this.paying = true
      this.$apis.payment
        .payInWx({
          pay_type: 2,
          pay_mode: 3,
          ...getSearch(),
          product_sn: 'PRODUCT_SN_CQ_7'
        })
        .then((result) => {
          window.WeixinJSBridge.invoke(
            'getBrandWCPayRequest',
            result,
            (res) => {
              this.paying = false
              if (res.err_msg === 'get_brand_wcpay_request:ok') {
                this.$router.push({
                  name: 'PayLoading',
                  query: {
                    ...this.$route.query,
                    productSn: 'PRODUCT_SN_CQ_7'
                  }
                })
              }
            }
          )
        })
        .catch((err) => {
          this.paying = false
          this.payErrorBack(err)
        })
    }
  }
}
</script>
